import React from 'react'
import {
  CAvatar,
  CDropdown,
  CDropdownItem,
  CDropdownMenu,
  CDropdownToggle,
} from '@coreui/react'
import {
  cilLockLocked,
  cilUser,
} from '@coreui/icons'
import CIcon from '@coreui/icons-react'

import { getAuth, signOut } from 'firebase/auth'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'
import { useEffect } from 'react'
import { collection, getDocs, query, where } from 'firebase/firestore'
import { db } from 'src/firebase/FirebaseProvider'
import LoadingOverlay from 'react-loading-overlay'

const AppHeaderDropdown = ({ handleLoading }) => {

  const navigate = useNavigate()
  const [avatar, setAvatar] = useState()
  const [user, setUser] = useState(JSON.parse(localStorage.getItem("user")))

  const goToProfile = () => {
    navigate("/profile")
  }

  const signOutUser = () => {
    handleLoading(true)
    signOut(getAuth()).then(rs => {
      handleLoading(false)
      localStorage.clear()
      navigate("/login")
    }).catch(rs1 => {
      handleLoading(false)
      Swal.fire({
        title: "Failed",
        text: "Failed to sign out user. Please try again",
        icon: "warning",
        confirmButtonColor: "#006f95"
      })
    })
  }

  useEffect(() => {
    if (user) {
      const q = query(collection(db, "users"), where("phoneNo", "==", user.phoneNumber));
      getDocs(q).then(rs => {
        rs.forEach(doc => {
          localStorage.setItem("userDetails", JSON.stringify(doc.data()))
          setAvatar(doc.data().profile)
        })
      });
    }
  }, [])


  return (
    
      <CDropdown variant="nav-item">
        <CDropdownToggle placement="bottom-end" className="py-0" caret={false}>
          <CAvatar src={avatar || 'https://cdn-icons-png.flaticon.com/512/6522/6522516.png'} size="sm" />
        </CDropdownToggle>
        <CDropdownMenu className="pt-0" placement="bottom-end">
          {/* <CDropdownHeader className="bg-light fw-semibold py-2">Settings</CDropdownHeader> */}
          <CDropdownItem onClick={goToProfile}>
            <CIcon icon={cilUser} className="me-2" />
            Profile
          </CDropdownItem>
          <CDropdownItem onClick={signOutUser}>
            <CIcon icon={cilLockLocked} className="me-2" />
            Log Out
          </CDropdownItem>
        </CDropdownMenu>
      </CDropdown>
  )
}

export default AppHeaderDropdown
